.islider-outer {
    display: block;
    position: relative;
    list-style: none;
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
}
.islider-outer > li {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    list-style: none;
    overflow: hidden;
    display: -moz-box;
    display: -webkit-box;
    display: box;
    -webkit-box-pack: center;
    box-pack: center;
    -webkit-box-align: center;
    box-align: center;
}
.islider-img > img {
    max-width: 100%;
    max-height: 100%;
}
.islider-sliding {
    z-index: 0;
}
.islider-sliding-focus {
    z-index: 1;
}
.islider-dot-wrap {
    position: absolute;
    margin: 0 auto;
    padding: 0;
    left: 0;
    right: 0;
